import React from "react";
import s from "./Post.module.css";

export const Post = (props) => {
  return (
    <div className={s.item}>
      <img
        src="data:image/jpeg;base64,"
        alt="avatarka"
      />
      {props.message}
      <div>
        <span>Like!</span>
        <span>{props.likesCount}</span>
      </div>
      <div>
        <span>Dislike</span>
      </div>
    </div>
  );
};
